<template>
  <div v-if="datalist">
    <!-- header start -->
    <oHeader @back="jump" v-scroll class="header">{{datalist.name}}</oHeader>
    <div class="leftIcon">
      <span class="iconfont iconarrowLeft"></span>
    </div>
    <!-- header end -->

    <div class="detils-logo">
      <img :src="datalist.poster" alt />
    </div>
    <div class="detils-text">
      <h2>{{datalist.name}}</h2>
      <p>{{datalist.category}}</p>
      <p>{{datalist.premiereAt | filterTime}}</p>
      <p>{{datalist.nation}} | {{datalist.runtime}}分钟</p>
      <div @click="msg=!msg" class="text">
        <span :class="{oH:msg}">{{datalist.synopsis}}</span>
        <i v-if="!msg" class="iconfont iconxiangxiajiantou"></i>
        <i v-else class="iconfont iconbubble_arrow_up"></i>
      </div>
    </div>
    <!-- 演员图片 起始 -->
    <div class="actors-pic">
      <h2>演职人员</h2>
      <Loop cName="actors-loop" class="swiper-loop">
        <div
          class="swiper-slide"
          v-for="(data,index) in datalist.actors"
          :key="index"
          v-swiper="{index:index,datalist:datalist.actors,cName:'actors-loop',num:3.8,space:10}"
          @click="bigPic(index)"
        >
          <img :src="data.avatarAddress" alt />
          <p style="color:black;fontSize:14px">{{data.name}}</p>
          <span>{{data.role}}</span>
        </div>
      </Loop>
    </div>
    <!-- 演员图片 结尾 -->

    <!-- 剧照 起始 -->
    <div class="state-pic">
      <div class="state-header">
        <h2>剧照</h2>
        <p @click="mask=true">
          全部({{datalist.photos.length}})
          <span class="iconfont iconyoujiantou1"></span>
        </p>
      </div>
      <Loop cName="state-loop" class="swiper-loop">
        <div
          class="swiper-slide"
          v-for="(data,index) in datalist.photos"
          :key="index"
          v-swiper="{index:index,datalist:datalist.photos,cName:'state-loop',num:2.3,space:10}"
        >
          <img :src="data" alt />
        </div>
      </Loop>
    </div>
    <!-- 剧照 结尾 -->

    <!-- mask start -->
    <div class="maskBox" v-if="mask">
      <oHeader @back="mask=false">{{datalist.name}}</oHeader>
      <!-- <div class="maskpic" v-for="(data,index) in datalist.photos" :key="index"> 
              <img :src="data" alt="">
      </div>-->
      <photos class="oPhoto" v-for="(data,index) in datalist.photos" :key="index" :data="data"></photos>
    </div>
    <!-- mask end -->
  </div>
</template>

<script>
import oHeader from "@/components/oHeader";
import photos from "@/view/detils/photos";
import { instance } from "@/utils/http";
import Loop from "@/components/Loop";
import Vue from "vue";
import { ImagePreview } from "vant";

// 全局注册
Vue.use(ImagePreview);

export default {
  components: {
    Loop,
    oHeader,
    photos
  },
  data() {
    return {
      datalist: null,
      msg: false,
      mask: false
    };
  },
  methods: {
    jump() {
      this.$router.back();
    },
    bigPic(index) {
      ImagePreview({
          //灵活运用map filter  返回数组
        images: this.datalist.actors.map(item=>item.avatarAddress),
        startPosition:index
      });
    }
  },
  created() {
    instance(`/gateway?filmId=${this.$route.params.id}&k=8232682`, {
      headers: {
        "X-Host": "mall.film-ticket.film.info"
      }
    }).then(res => {
      this.datalist = res.data.data.film;
    });
  }
};
</script>
<style lang="scss" scoped>
.header {
  transition: 0.6s;
}
.leftIcon {
  position: fixed;
  top: 5px;
  left: 2px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: lightgray;
  text-align: center;
  span {
    font-size: 25px;
    line-height: 30px;
  }
}
.maskBox {
  width: 100%;
  height: 100%;
  background: white;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 7;
  flex-flow: wrap;
  .oPhoto {
    margin-top: 0.45rem;
  }
}
.state-pic {
  border-top: 5px solid lightblue;
  width: 100%;
  padding-bottom: 0.5rem;
  .state-header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.15rem;
    h2 {
      line-height: 0.4rem;
      color: black;
    }
  }
  img {
    width: 1.5rem;
    height: 1rem;
  }
}
.actors-pic {
  border-top: 5px solid lightblue;
  padding-bottom: 0.1rem;
  width: 100%;
  text-align: center;
  margin-top: 0.15rem;
  padding-left: 0.15rem;
  h2 {
    text-align: left;
    color: black;
    line-height: 40px;
  }
  img {
    width: 0.85rem;
    height: 1.17rem;
  }
}
.detils-logo {
  width: 100%;
  height: 2.4rem;
  img {
    width: 100%;
    height: 100%;
  }
}
.detils-text {
  padding-left: 0.15rem;
  .text {
    width: 100%;
    position: relative;
    i {
      position: absolute;
      bottom: -0.15rem;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  h2 {
    color: black;
  }
  p {
    line-height: 25px;
  }
  span {
    display: block;
    height: 0.38rem;
    transition: 0.6s;
    overflow: hidden;
  }
  .oH {
    height: 0.8rem;
  }
}
</style>